<template>
  <div v-bind:style="`background-color: ${profileData.bgColor}; color: ${invertHex}`" class="card profile m-3 m-md-4 p-3 p-lg-4">

    <ProfileHeader :edit-mode="false" :uid="uid" :user-name="userName"></ProfileHeader>

    <!-- Cover Image -->
    <img class="card-img-top cover-img" v-bind:src="profileData.coverImg"/>
    <!-- /Cover Image -->

    <div class="card-body text-center">

      <!-- Profile Picture -->
      <img class="avatar rounded-circle" v-bind:style="{ 'border-color': invertHex }" :src="profileData.photoURL"/>
      <!-- /Profile Picture -->

      <!-- Display Name -->
      <h4 class="card-title">{{ profileData.displayName }}</h4>
      <!-- /Display Name -->

      <!-- Short Bio -->
      <h6 class="card-subtitle mb-2 text-muted">{{ profileData.shortBio }}</h6>
      <!-- /Short Bio -->

      <!-- Social Links -->
      <div class="d-flex justify-content-center mt-4">
        <a :href="socialLink.link" target="_blank" class="p-3 text-reset  is-clickable"
          v-for="(socialLink, key) in populatedSocialLinks" :key="key"
        >
          <font-awesome-icon class="fa-2x" :icon="socialLink.icon" />
        </a>
      </div>
      <!-- /Social Links -->

    </div>

    <hr v-bind:style="`background-color: ${invertHex}`" class="mx-4" />

    <!-- Long Bio -->
    <h4 class="font-weight-bold text-muted">Bio</h4>
    <h6 v-if="!centerBio">{{ profileData.longBio }}</h6>
    <h5 v-if="centerBio" class="text-center">{{ profileData.longBio }}</h5>
    <!-- /Long Bio -->

    <hr v-bind:style="`background-color: ${invertHex}`" class="mx-4" />

    <div v-if="category" class="mb-3">
      <h4 class="text-capitalize font-weight-bold text-muted">#{{category}} 🏷️</h4>
      <a class="text-reset display-6" href="#" v-on:click.self.prevent="$router.push({ path: `/${uid}` })">👀 View all categories</a>
    </div>

    <!-- Content Cards -->
    <div class="row mt-3 text-secondary" v-bind:class="`row-cols-${profileData.numMobileColumns} row-cols-md-${profileData.numDesktopColumns}`">
      <ViewContentCard v-for="(contentCard, contentCardId) in sortedCards" :key="contentCardId"
        :contentCard="contentCard"
        :contentCardId="contentCardId"
        :ownerId="uid"
        :currentCategory="category"
      >
      </ViewContentCard>
    </div>
    <!-- /Content Cards -->

    <div v-if="totalRows > perPage" class="my-5">
      <b-pagination
        class="pagination"
        align="center"
        v-model="currentPage"
        :total-rows="totalRows"
        :per-page="perPage"
      ></b-pagination>
    </div>

  </div>
</template>

<script language="ts">
import ProfileHeader from '@/components/ProfileHeader'
import ViewContentCard from './ViewContentCard'

export default {
  components: {
    ProfileHeader,
    ViewContentCard
  },
  props: {
    uid: String,
    userName: String,
    profileData: Object,
    category: String
  },
  data () {
    return {
      perPage: 10,
      currentPage: 1
    }
  },
  computed: {
    centerBio () { return this.profileData.longBio?.length <= 140 },
    compColor () { return this.profileData?.bgColor },
    populatedSocialLinks () {
      if (!this.profileData?.socialLinks) return []
      return Object.keys(this.profileData.socialLinks).reduce((p, c) => {
        if (!this.profileData.socialLinks[c].link.startsWith('https://...')) p[c] = this.profileData.socialLinks[c]
        return p
      }, {})
    },
    sortedKeys () {
      const { contentCards } = this.profileData
      if (!contentCards) return []
      const allKeys = Object.keys(this.profileData.contentCards)

      if (this.category) {
        return allKeys.filter(ccKey => this.category === this.profileData.contentCards[ccKey].category).reverse()
      } else {
        return allKeys.reverse()
      }
    },
    totalRows () { return this.sortedKeys.length },
    sortedCards () {
      const skip = this.currentPage > 1 ? (this.currentPage - 1) * this.perPage : 0
      const limit = skip + this.perPage
      return this.sortedKeys.slice(skip, limit).reduce((p, c) => {
        p[c] = this.profileData.contentCards[c]
        return p
      }, {})
    },
    invertHex () {
      const hex = this.compColor?.slice(1)
      return hex ? `#${(Number(`0x1${hex}`) ^ 0xFFFFFF).toString(16).substr(1).toUpperCase()}` : ''
    }
  }
}
</script>

<style lang="css">
/* Custom CSS here */

.profile .avatar {
  height: 150px;
  width: 150px;
  object-fit: cover;
  margin-top: -6rem;
  margin-bottom: 1rem;
}

.profile .cover-img {
  height: 250px;
  object-fit: cover;
}

.is-clickable:hover {
  cursor: pointer;
  transform: scale(1.05,1.05);
}
</style>
