<template>
  <div class="col mb-4" :id="`cc-${contentCardId}`">
    <div class="card h-100">
      <div class="card-img-top embed-responsive embed-responsive-4by3">

        <!-- external_link -->
        <a v-if="contentCard.type == 'remote_link'" target="_blank" :href="contentCard.postUrl" class="text-reset">
          <font-awesome-icon :id="`cc-icon-${contentCardId}`" class="embed-responsive-item p-4" :icon="contentCard.icon"/>
        </a>
        <!-- /external_link -->

        <!-- ig_image remote_image -->
        <a v-if="['ig_image', 'remote_image'].includes(contentCard.type)" target="_blank" :href="contentCard.postUrl" class="text-reset">
          <img style="object-fit: cover;" class="embed-responsive-item" :src="contentCard.url"/>
        </a>
        <!-- /ig_image remote_image -->

        <!-- ig_video -->
        <video v-if="contentCard.type === 'ig_video'" class="embed-responsive-item" controls>
          <source :src="contentCard.url">
        </video>
        <!-- /ig_video -->

        <!-- iframe -->
        <iframe v-if="contentCard.type === 'iframe'" class="embed-responsive-item" :src="contentCard.url"
          allowtransparency="true"
          frameborder="0"
          allow="accelerometer; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
        <!-- /iframe -->

      </div>
      <div class="card-body">

        <!-- title -->
        <a v-if="contentCard.postUrl && contentCard.postUrl.trim().length" target="_blank" :href="contentCard.postUrl" class="text-reset"><h5 class="card-title">{{contentCard.title}}</h5></a>
        <h5 v-if="!contentCard.postUrl || !contentCard.postUrl.trim().length" class="card-title">{{contentCard.title}}</h5>
        <!-- /title -->

        <!-- description -->
        <a v-if="contentCard.postUrl && contentCard.postUrl.trim().length" target="_blank" :href="contentCard.postUrl" class="text-reset"><p class="card-text">{{contentCard.description}}</p></a>
        <p v-if="!contentCard.postUrl || !contentCard.postUrl.trim().length" class="card-text">{{contentCard.description}}</p>
        <!-- /description -->

      </div>

      <div class="d-inline pl-3 pb-1 pr-2 mb-2">
        <a class="text-reset float-left" v-if="!currentCategory && contentCard.category" href="#" v-on:click.self.prevent="$router.push({ path: `/${ownerId}/category/${contentCard.category}` })">#{{contentCard.category + ' 🏷️'}}</a>
        <font-awesome-icon class="mt-1 is-clickable float-right" v-if="contentCard.postUrl && contentCard.postUrl.trim().length" @click="showSocialShare(contentCard.postUrl, contentCard.title)" :icon="['fa', 'share-alt']"/>
      </div>
    </div>
  </div>

</template>

<script language="ts">
import EventBus from '@/utils/EventBus'

export default {
  props: {
    ownerId: String,
    contentCardId: String,
    contentCard: Object,
    currentCategory: String
  },
  methods: {
    showSocialShare (url, socialShareText) {
      EventBus.$emit('SHOW_SOCIAL', { url, socialShareText })
    }
  }
}
</script>
