<template>
  <div>
    <img src="@/assets/beta.png" width="80px" class="float-left m-0 p-0" />
    <span class="float-right">
      <a
        class="text-reset p-3"
        target="_blank"
        href="https://blog.instab.io/#/guide/customise-your-profile-like-a-pro"
      >
        <font-awesome-icon class="fa-lg is-clickable" :icon="['fa', 'question']" />
      </a>

      <a v-b-modal.modal-share @click="showSocialShare"
        class="text-reset p-3"
      >
        <font-awesome-icon
          class="fa-lg is-clickable"
          :icon="['fa', 'share-alt']"
        />
      </a>

      <a v-if="isLoggedIn && !editMode"
        class="text-reset p-3"
        @click="$router.push(`/${currentUserId}/edit`)"
      >
        <font-awesome-icon
          class="fa-lg is-clickable"
          :icon="['fa', 'pencil-alt']"
        />
      </a>

      <a v-if="isLoggedIn && editMode"
        @click="$router.push(`/${currentUserId}/`)"
        class="text-reset p-3"
      >
        <font-awesome-icon
          class="fa-lg is-clickable"
          :icon="['fa', 'eye']"
        />
      </a>

      <a v-if="isLoggedIn"
        class="text-reset p-3"
        @click="signOut"
      >
        <font-awesome-icon
          class="fa-lg is-clickable"
          :icon="['fa', 'sign-out-alt']"
        />
      </a>

      <a v-if="!isLoggedIn"
        class="text-reset p-3"
        @click="$router.push('/')"
      >
        <font-awesome-icon
          class="fa-lg is-clickable"
          :icon="['fa', 'sign-in-alt']"
        />
      </a>
    </span>

    <!-- Modal Share -->
    <b-modal id="modal-share" title="Share this Bio!" class="pt-5 mt-5"
      hide-footer
      :body-bg-variant="'light'"
      :body-text-variant="'dark'"
    >
      <p class="my-2"><strong>🌐 Regular Link</strong></p>
      <p class="mt-1"><a :href="shareProfileLink">{{shareProfileLink}}</a></p>
      <hr />
      <p class="my-2"><strong>💜 Insta Link</strong> (Insta Bio field can't include the <strong>#</strong> symbol)</p>
      <p class="mt-1"><a :href="shareProfileInstaLink">{{shareProfileInstaLink}}</a></p>
    </b-modal>
    <!-- /Modal Share -->
  </div>
</template>

<script language="ts">
import { auth } from '@/firebase.config'
import EventBus from '@/utils/EventBus'

export default {
  props: {
    editMode: Boolean,
    uid: String,
    userName: String
  },
  data () {
    return {
      currentUserId: auth?.currentUser?.uid,
      isLoggedIn: auth?.currentUser
    }
  },
  computed: {
    currentUserRoute () { return this.userName ? `p/${this.userName}` : this.uid },
    shareProfileLink () { return `https://instab.io/#/${this.currentUserRoute}` },
    shareProfileInstaLink () { return `https://instab.io/profile?${this.userName ? 'p='.concat(this.userName) : 'id='.concat(this.uid)}` }
  },
  methods: {
    async signOut () {
      await auth.signOut()
      this.$router.push('/')
    },
    showSocialShare () {
      EventBus.$emit('SHOW_SOCIAL', { url: this.shareProfileLink, socialShareText: this.userName })
    }
  }
}
</script>
