<template>
  <div>

    <Spinner v-if="loading"></Spinner>
    <ViewProfile v-if="!loading"
      :profile-data="profileData"
      :user-name="userName"
      :uid="uid"
      :category="category"
    >
    </ViewProfile>
  </div>
</template>

<script language="ts">
import Spinner from '@/components/Spinner'
import { profilesCollection } from '@/firebase.config'
import ViewProfile from '@/components/ViewProfile'

export default {
  metaInfo ({ userName }) {
    return {
      title: userName ?? 'View Profile'
    }
  },
  components: {
    ViewProfile,
    Spinner
  },
  props: {
    uid: String,
    userName: String,
    category: String
  },
  data () {
    return {
      loading: true,
      profileData: {}
    }
  },
  async mounted () {
    this.loading = true
    const { uid } = this
    const profileRef = profilesCollection.doc(uid)
    const profileDoc = await profileRef.get()
    if (!profileDoc || !profileDoc.exists) {
      this.$router.push('/404')
    } else {
      this.profileData = profileDoc.data()
      this.loading = false
    }
  },
  beforeRouteUpdate () { // Change profile, force route re-entring
    document.location.reload()
  }
}
</script>
